.container {
    width: 100%;
    height: 70px;
}

.horizontalSection {
    width: 100%;
    height: 100%;
    display: flex;
    align-content: center;
    justify-content: space-between;
    flex-direction: row;
    composes: boxShadow from '../../CommonUI/colors.css';
}

.logo {
    width: 15%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.rightEnd {
    width: 85%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.menu {
    width: 30%;
    margin-right: 100px;
    padding: 14px 14px;
    /* border: 1px solid black; */
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.menuItem {
    /* border: 1px solid black; */
    padding: 4px 16px;
    cursor: pointer;
    font-weight: 500;
    composes: buttonRadiusSmall from '../../CommonUI/colors.css';
    composes: subText from '../../CommonUI/colors.css';
    composes: hoverColor from '../../CommonUI/colors.css';
    transition: 0.3s;
}

.mobileMenu {
    composes: menu;
    margin-right: 0;
    width: 10%;
    border: 1px solid black;
}